<template>
  <div id=app>
    <div class="flexwrap">
      <div class="empty-space"></div>
      <div class="main-content">
        <Stages v-bind:stages="stages" v-on:change-index="changeIndex" />
        <ListOfSteps v-bind:steps="steps" v-on:toggle-description="toggleDescription"/>
      </div>
      <div class="empty-space"></div>
    </div>
  </div>
</template>

<script>
//import HelloWorld from './components/HelloWorld.vue'
import Stages from './components/Stages.vue'
import ListOfSteps from './components/ListOfSteps.vue'

export default {
  name: 'App',
  components: {
    Stages,
    ListOfSteps
  },
  data(){
    let cross = {
      index: 0,
      name: "Cross",
      steps: [
        {
          name: "Cross on bottom",
          description: "As a beginner you will probably find it easier to solve the cross on top, but solving it on bottom it better, and it's easier to switch if you do it early.",
          showDescription: false
        }, // <a target="_blank" rel="noopener noreferrer" href="http://www.google.com/">This is a link</a>
        {
          name: "8 moves or less",
          description: "Every cross can be solved with a maximum of 8 moves. If your cross often takes more than that, then you should work on efficency. Look for how the moves to solve the easy pieces can be used to help solving the harder ones.",
          showDescription: false
        },
        {
          name: "Plan cross in inspection",
          description: "Try to plan the entire cross (or maybe just 3 pieces in the beginning), and then solve it with your eyes closed. Don't worry about inspection time while doing this. Idealy you want to look for what to do next while solving the cross, not look at the cross, though this is a long proces with gradual improvement.",
          showDescription: false
        },
        {
          name: "Cross + corner",
          description: "During inspection, find an F2L corner that will end up in the top layer after the cross is done. This will help with the transition from cross to F2L, as you only need to look for the corresponding edge while solving the cross.",
          showDescription: false
        },
        {
          name: "Cross + 1",
          description: "Plan out both cross and the first pair during inspection.",
          showDescription: false
        },
        {
          name: "X-cross",
          description: "Solve cross and first pair simultaniously. This should not replace Cross+1, but rather be an additional tool for when the oppertunity arise.",
          showDescription: false
        }
      ]
    }

    let f2l = {
      index: 1,
      name:"F2L",
      steps: [
          {
            name: "Basic",
            description: "Do it. <code>Hello</code>",
            showDescription: false
          },
          {
            name: "Improve",
            description: "Do it better.",
            showDescription: false
          }
      ]
    }

    let ll = {
      index: 2,
      name: "LL",
      steps: [
        {
          name: "Beginner variant",
          description: "Orient edges with <code>F sexy F'</code>  , corners with Sune.",
          showDescription: false
        },
        {
          name: "2 Look OLL",
          description: 'Edges with one of 4 algs, corners with one of 7.',
          showDescription: false
        }
      ]
    }

    // let pll = {
    //   index: 3,
    //   name: 'PLL',
    //   steps: [
    //     {
    //       name: 'Beginner variant',
    //       description: 'Do it easy.',
    //       showDescription: false
    //     },
    //     {
    //       name: '2 Look PLL',
    //       description: `
    //       <p>Do the ting.</p>
    //       <p>Do the other thing.</p>
    //       `,
    //       showDescription: false
    //     },
    //     {
    //       name: 'Full PLL',
    //       description: 'It\'s easy.',
    //       showDescription: false
    //     }
    //   ]
    // }
    let stages = [cross, f2l, ll];
    
    return{
      stages: stages,
      steps: stages[0].steps,
      currentIndex: 0
    }
  },
  methods: {
    changeIndex(index, element){
      this.steps = this.stages[index].steps;
      this.currentIndex = index;

      let stages = document.getElementsByClassName("stage");
      stages.forEach(stage => stage.classList.add("stage-background-color"));
      element.classList.remove("stage-background-color");
      element.classList.add("selected-stage-background-color");
    },
    toggleDescription(name){
      
      let step = this.steps.filter(step => step.name == name)[0];
      if(step.showDescription == true){
        step.showDescription = false;
        document.cookie = (`${this.stages[this.currentIndex].name}=${-1}`);
        return;
      }

      this.steps.forEach(step => step.showDescription = step.name != name ? false : true);
      
      step.showDescription = true;

      let indexOfShownDescription = this.steps.findIndex(step => step.showDescription==true);

      document.cookie = (`${this.stages[this.currentIndex].name}=${indexOfShownDescription}`);
    }
  },
  mounted(){
    let firstStage = document.getElementsByClassName("stage")[0];
    firstStage.classList.remove("stage-background-color");
    firstStage.classList.add("selected-stage-background-color");

    let cookies = organizeCookies();

    setupForSelectedIndexes(this.stages, cookies);
  }
}

function organizeCookies(){
  let cookies = document.cookie.split("; ")
  cookies = cookies.map(cookie => cookie.split("="));
  return cookies;
}

function setupForSelectedIndexes(stages, cookies){
  for(let i=0; i<stages.length; i++){
    let currentStep = stages[i].name;
    let selectedCookie = cookies.find(cookie => cookie[0] == currentStep);
    //console.log("SelectedCookie #" + i + " " + selectedCookie);
    if(selectedCookie[1] >= 0){
      stages[i].steps[parseInt(selectedCookie[1])].showDescription = true;
    }
  }
}
</script>

